<template>
  <div class="login-page">
    <div class="login-window">
      <div class="login-form">
        <div class="form-options">
          <div class="logo">
          </div>
          <div class="options">
              <input type="button" value="注册" class="sign-up-option" @click="login"/>
              <input type="button" value="登录" class="sign-in-option active-option" @click="login"/>
          </div>
        </div>
        <div class="form-input">
          <input v-model="username" type="text" class="input-area first-input-margin" name='username' placeholder="账户名" ref="log_username"/>
          <input v-model="password" type="password" class="input-area second-input-margin" name="password" placeholder="密码" ref="log_password"/>
        </div>
        <div class="form-button">
            <input type="button" value="登录" class="login-btn" @click="login"/>
        </div>
      </div>
      <div class="window-info"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoginPage',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  components: {
  }
}
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "~assets/stylus/base"
  .login-page
    position fixed
    top 0
    left 0
    width 100%
    height 100%
    min-width 1000px
    background-image $pic-background
    background-repeat no-repeat
    .login-window
      background inherit
      background-attachment fixed
      width $wrap-width
      height $wrap-height
      position absolute
      left 0
      top 0
      right 0
      bottom 0
      margin auto
      box-shadow 0px 0px 0px 1000px rgba(0,0,0,0.6);
      border-radius $border-radius-b
      overflow hidden
      .login-form
        background $color-background
        width 50%
        height 100%
        float left
        .form-options
          width 100%
          height 25%
          display flex
          .options
            display flex
            flex-direction row
            justify-content center
            align-items center
          .sign-up-option
            z-index 1
            width 110px
            height 50px
            background linear-gradient(to right,#8cb0b9,#95a7c0,#959ec7)
            border-radius 25px 0px 0px 25px
            color $color-text-lll
            font-family $font
            font-size $font-size-medium-x
            float left
            box-shadow 0px 7px 50px rgba(35,47,61,0.2)
          .sign-in-option
            z-index 1
            width 110px
            height 50px
            color $color-text-lll
            background linear-gradient(to right,#8cb0b9,#95a7c0,#959ec7)
            border-radius 0px 25px 25px 0px
            font-family $font
            font-size $font-size-medium-x
            float left
            box-shadow 0px 7px 50px rgba(35,47,61,0.3)
          .active-option
            z-index 10
            color $color-text-xl
            box-shadow 0px 7px 50px rgba(35,47,61,0.5)
            background linear-gradient(to right,#90e0f8,#a7ceff,#b0c0ff)
          .logo
            background $pic-logo no-repeat center center
            width 50%
          .options
            width 50%
        .form-input
          width 100%
          height 45%
          display flex
          flex-direction column
          justify-content flex-end
          align-items center
          .input-area
            width 75%
            height 90px
            background $color-background-m
            border-radius 20px
            font-family $font
            font-size $font-size-large
            text-indent 2em
          input::-webkit-input-placeholder
            color $color-text-md
          .first-input-margin
            margin-bottom 60px
          .second-input-margin
            margin-bottom 45px
        .form-button
          width 100%
          height 30%
          display flex
          flex-direction column
          justify-content center
          align-items center
          .login-btn
            width 45%
            height 100px
            color $color-text-xl
            background  linear-gradient(to right,#1ec7ff,#6591ff)
            font-family $font
            font-size $font-size-large-x
            border-radius $border-radius-bbb
      .window-info
        width 50%
        height 100%
        float left

</style>



